<template>
  <div class="gw-index">
    <header class="header">
      <a class="header-logo" href="javascript:void(0)" >
        <img class="logo" src="@assets/img/logos-header.jpg" alt="logo">
      </a>
      <div class="header-menu">
      	<router-link v-for="(item,index) in nav" class="header-menu-item" :class="index==nowRouter?'check':''"  :to="{path:item.url}" @click.native="addCl(index)">
      		<span>{{item.name}}</span>
      	</router-link>
      	

      </div>
      <div class="header-opt">
        <span class="user" title="当前登陆用户">
          <span class="iconfont">&#xe62e;</span>{{$store.state.loginMsg}}</span>
    
        <span :class="screenAll?'iconfont icon-pullScreenexit':'iconfont icon-pullScreen'" :title="screenAll?'还原':'全屏'" @click="fullScreen" ></span>
        <span class="iconfont" title="退出登陆" @click="logout">&#xe641;</span>
      </div>
    </header>
    <div class="main">
       <router-view class="router-page"></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      screenAll: false,//是否全屏
      nowRouter:0,
      adminMeeting:false,
      adminVisitor:false,
      adminCard:false,
      adminInter:false,
      yunyin:true,
      nav:[],
      admin:[],
      router:[],
      cla:[]
    };
  },mounted(){
  	this.adminAuthor()
  },
  methods: {
  	adminAuthor(){
  		this.Axios.post("/api/Event/get_mem_infor_work",{workNo:localStorage.userName},{headers:{'Authorization':localStorage.gw_token}}).then(res=>{
  			let dat=res.data.HttpData.data;
  			for(var value of dat){
  				let type=value.type;
					if(type==6){
							this.nav.push({
								name:"会议系统",
								url:"meeting"
							})

					}else if(type==7){
							this.nav.push({
								name:"访客系统",
								url:"victor"
							})

					}else if(type==0||type==1||type==3||type==5){
							let has=false;
							let arr=this.nav;
							for(var i=0;i<arr.length;i++){
								 if(arr[i].url=="card"){
									 	has=true;
								 }
							}
							if(!has){
								this.nav.push({
									name:"一卡通系统",
									url:"card"
								})
							}
					}else if(type==8){
							this.nav.push({
								name:"资产管理",
								url:"property"
							})
					}
					
  			}
  			this.nav.push({
						name:"运营情况",
						url:"operation"
				})
				this.$router.push(this.nav[0].url);
				
  		})
  	},
    getAuth() {
      // 判断appkey和infokey是否存在正确
      if (window.localStorage["gw_token"]) {
        this.$store.dispatch("reflashSet");
        this.Axios.defaults.headers.common[
          "Authorization"
        ] = this.$store.state.gwToken;
        this.Axios.get("/api/server/auth_name")
          .then(rt => {
            let data = rt.data.HttpData;
            if (data.code !== 200) {
              this.$router.push("/login");
            } else {
              console.log("密钥验证成功,当前连接的服务:[", data.data, "]");
            }
          })
          .catch(err => {
            
            console.log("密钥验证失败，请检查登陆设置!");
          });
      } else {
        this.$router.push("/");
      }
    },logout() {
      // 退出登陆
      this.$Modal.confirm({
        title: '操作提示',
        content: '是否注销当前登陆？',
        onOk: () => {
          window.localStorage.removeItem("gw_token")
          window.localStorage.removeItem("login_msg")
          // this.navList.splice(0, this.navList.length)
          this.$router.replace("/login")
        }
      })
    },fullScreen(){
      //全屏事件
            if ($("body").hasClass("full-screen")) {
              this.screenAll = false;
                var de = document;
                if (de.exitFullscreen) {
                    de.exitFullscreen();
                } else if (de.mozCancelFullScreen) {
                    de.mozCancelFullScreen();
                } else if (de.webkitCancelFullScreen) {
                    de.webkitCancelFullScreen();
                }
                $("body").removeClass("full-screen");
            }
            else {this.screenAll = true;
                var de = document.documentElement;
                if (de.requestFullscreen) {
                    de.requestFullscreen();
                } else if (de.mozRequestFullScreen) {
                    de.mozRequestFullScreen();
                } else if (de.webkitRequestFullScreen) {
                    de.webkitRequestFullScreen();
                } else if (de.msRequestFullscreen) {
                    de.msRequestFullscreen();
                }
                $("body").addClass("full-screen");
            }
    },
    logoClickEvent(){
//    this.$router.push("home")
//    this.setNav()
    },addCl(id){
    	this.nowRouter=id;
    }
  }

}
</script>

<style lang="scss" src="../assets/styles/sass/index.scss"></style>



